import { HttpClient } from '@angular/common/http';
import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import IMask from 'imask';
import * as moment from 'moment';
import { TipoCartaCredito } from 'src/app/shared/models/enum';

@Component({
  selector: 'app-credit-card',
  templateUrl: './credit-card.component.html',
  styleUrls: ['./credit-card.component.scss']
})
export class CreditCardComponent implements OnInit, AfterViewInit {

  @Input() nameInput: HTMLInputElement;
  @Input() cardNumberInput: HTMLInputElement;
  @Input() expirationDateInput: HTMLInputElement;
  @Input() securityCodeInput: HTMLInputElement;

  @Output() creditCardTypeDetected: EventEmitter<TipoCartaCredito> = new EventEmitter();

  public flipped: boolean = false;
  public year: string = (moment().year() + 1).toString().slice(2);

  public americanExpress: string;
  public visa: string;
  public diners: string;
  public discover: string;
  public jcb: string;
  public maestro: string;
  public mastercard: string;
  public unionpay: string;

  public ccImage: string = '';
  public color: string = 'grey';

  public name: string = 'Mario Rossi';
  public number: string = '0123 4567 8910 1112';
  public expiration: string = '01/' + this.year;
  public securityCode: string = '985';

  constructor(private http: HttpClient) { }

  ngOnInit() {
    this.http.get('/assets/images/booking/credit-card/american-express.svg', { responseType: 'text' }).subscribe({
      next: (data: any) => {
        this.americanExpress = data;
      },
      error: () => { }
    });

    this.http.get('/assets/images/booking/credit-card/visa.svg', { responseType: 'text' }).subscribe({
      next: (data: any) => {
        this.visa = data;
      },
      error: () => { }
    });

    this.http.get('/assets/images/booking/credit-card/diners.svg', { responseType: 'text' }).subscribe({
      next: (data: any) => {
        this.diners = data;
      },
      error: () => { }
    });

    this.http.get('/assets/images/booking/credit-card/discover.svg', { responseType: 'text' }).subscribe({
      next: (data: any) => {
        this.discover = data;
      },
      error: () => { }
    });

    this.http.get('/assets/images/booking/credit-card/jcb.svg', { responseType: 'text' }).subscribe({
      next: (data: any) => {
        this.jcb = data;
      },
      error: () => { }
    });

    this.http.get('/assets/images/booking/credit-card/maestro.svg', { responseType: 'text' }).subscribe({
      next: (data: any) => {
        this.maestro = data;
      },
      error: () => { }
    });

    this.http.get('/assets/images/booking/credit-card/mastercard.svg', { responseType: 'text' }).subscribe({
      next: (data: any) => {
        this.mastercard = data;
      },
      error: () => { }
    });

    this.http.get('/assets/images/booking/credit-card/unionpay.svg', { responseType: 'text' }).subscribe({
      next: (data: any) => {
        this.unionpay = data;
      },
      error: () => { }
    });
  }

  ngAfterViewInit() {
    let cardnumber_mask = IMask(this.cardNumberInput, {
      mask: [
        {
          mask: '0000 000000 00000',
          regex: '^3[47]\\d{0,13}',
          cardtype: 'american express'
        },
        {
          mask: '0000 0000 0000 0000',
          regex: '^(?:6011|65\\d{0,2}|64[4-9]\\d?)\\d{0,12}',
          cardtype: 'discover'
        },
        {
          mask: '0000 000000 0000',
          regex: '^3(?:0([0-5]|9)|[689]\\d?)\\d{0,11}',
          cardtype: 'diners'
        },
        {
          mask: '0000 0000 0000 0000',
          regex: '^(5[1-5]\\d{0,2}|22[2-9]\\d{0,1}|2[3-7]\\d{0,2})\\d{0,12}',
          cardtype: 'mastercard'
        },
        // {
        //     mask: '0000-0000-0000-0000',
        //     regex: '^(5019|4175|4571)\\d{0,12}',
        //     cardtype: 'dankort'
        // },
        // {
        //     mask: '0000-0000-0000-0000',
        //     regex: '^63[7-9]\\d{0,13}',
        //     cardtype: 'instapayment'
        // },
        {
          mask: '0000 000000 00000',
          regex: '^(?:2131|1800)\\d{0,11}',
          cardtype: 'jcb15'
        },
        {
          mask: '0000 0000 0000 0000',
          regex: '^(?:35\\d{0,2})\\d{0,12}',
          cardtype: 'jcb'
        },
        {
          mask: '0000 0000 0000 0000',
          regex: '^(?:5[0678]\\d{0,2}|6304|67\\d{0,2})\\d{0,12}',
          cardtype: 'maestro'
        },
        // {
        //     mask: '0000-0000-0000-0000',
        //     regex: '^220[0-4]\\d{0,12}',
        //     cardtype: 'mir'
        // },
        {
          mask: '0000 0000 0000 0000',
          regex: '^4\\d{0,15}',
          cardtype: 'visa'
        },
        {
          mask: '0000 0000 0000 0000',
          regex: '^62\\d{0,14}',
          cardtype: 'unionpay'
        },
        {
          mask: '0000 0000 0000 0000',
          cardtype: 'Unknown'
        }
      ],
      dispatch: (value: string, masked: any, flags: any) => {
        let number = (masked.value + value).replace(/\D/g, '');

        for (let mask of masked.compiledMasks) {
          let re = new RegExp(mask.regex);

          if (number.match(re) != null) {
            return mask;
          }
        }
      }
    });

    let expirationdate_mask = IMask(this.expirationDateInput, {
      mask: 'MM{/}YY',
      blocks: {
        YY: {
          mask: IMask.MaskedRange,
          placeholderChar: 'YY',
          from: 0,
          to: 99,
          maxLength: 2
        },
        MM: {
          mask: IMask.MaskedRange,
          placeholderChar: 'MM',
          from: 1,
          to: 12,
          maxLength: 2
        }
      }
    });

    let securitycode_mask = IMask(this.securityCodeInput, {
      mask: '0000'
    });

    cardnumber_mask.on("accept", () => {
      switch (cardnumber_mask.masked.currentMask['cardtype']) {
        case 'american express':
          this.ccImage = this.americanExpress;
          this.color = 'green';
          this.creditCardTypeDetected.emit(TipoCartaCredito.AmericanExpress);
          break;
        case 'visa':
          this.ccImage = this.visa;
          this.color = 'lime';
          this.creditCardTypeDetected.emit(TipoCartaCredito.Visa);
          break;
        case 'diners':
          this.ccImage = this.diners;
          this.color = 'orange';
          this.creditCardTypeDetected.emit(TipoCartaCredito.DinersClub);
          break;
        case 'discover':
          this.ccImage = this.discover;
          this.color = 'purple';
          this.creditCardTypeDetected.emit(TipoCartaCredito.DiscoveryCard);
          break;
        case 'jcb': case 'jcb15':
          this.ccImage = this.jcb;
          this.color = 'red';
          this.creditCardTypeDetected.emit(TipoCartaCredito.JCB);
          break;
        case 'maestro':
          this.ccImage = this.maestro;
          this.color = 'yellow';
          this.creditCardTypeDetected.emit(TipoCartaCredito.Maestro);
          break;
        case 'mastercard':
          this.ccImage = this.mastercard;
          this.color = 'lightblue';
          this.creditCardTypeDetected.emit(TipoCartaCredito.MasterCard);
          break;
        case 'unionpay':
          this.ccImage = this.unionpay;
          this.color = 'cyan';
          this.creditCardTypeDetected.emit(TipoCartaCredito.ChinaUnionPay);
          break;
        default:
          this.ccImage = '';
          this.color = 'grey';
          this.creditCardTypeDetected.emit(null);
          break;
      }
    });

    this.nameInput.addEventListener('input', () => {
      if (!this.nameInput.value) {
        this.name = 'Mario Rossi';
      } else {
        this.name = this.nameInput.value;
      }
    });

    cardnumber_mask.on('accept', () => {
      if (!cardnumber_mask.value) {
        this.number = '0123 4567 8910 1112';
      } else {
        this.number = cardnumber_mask.value;
      }
    });

    expirationdate_mask.on('accept', () => {
      if (!expirationdate_mask.value) {
        this.expiration = '01/' + this.year;
      } else {
        this.expiration = expirationdate_mask.value;
      }
    });

    securitycode_mask.on('accept', () => {
      if (!securitycode_mask.value) {
        this.securityCode = '985';
      } else {
        this.securityCode = securitycode_mask.value;
      }
    });

    this.nameInput.addEventListener('focus', () => {
      this.flipped = false;
    });

    this.cardNumberInput.addEventListener('focus', () => {
      this.flipped = false;
    });

    this.expirationDateInput.addEventListener('focus', () => {
      this.flipped = false;
    });

    this.securityCodeInput.addEventListener('focus', () => {
      this.flipped = true;
    });
  }
}