import { AfterViewInit, Component, Input } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { BaseComponent } from '../../base-component/base-component';

declare let SumUpCard;
declare let AxeptaSDKClient;

@Component({
  selector: 'app-pagamento-form-dialog',
  templateUrl: './pagamento-form-dialog.component.html',
  styleUrls: ['./pagamento-form-dialog.component.scss']
})
export class PagamentoFormDialogComponent extends BaseComponent implements AfterViewInit {

  @Input() html: boolean;
  @Input() title: string;
  @Input() text: string;
  @Input() sumupTransactionId: string;
  @Input() hobexTransactionId: string;
  @Input() axeptaTransactionId: string;
  @Input() paymentSuccessUrl: string;
  @Input() axeptaApiKeySmartCheckout: string;
  @Input() isTest: boolean;
  @Input() lingua: string;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public readonly HOBEX_JS_URL = 'https://oppwa.com/v1/paymentWidgets.js?checkoutId=';
  public readonly HOBEX_JS_URL_TEST = 'https://test.oppwa.com/v1/paymentWidgets.js?checkoutId=';

  public readonly AXEPTA_URL = 'https://pay.axepta.it';
  public readonly AXEPTA_URL_TEST = 'https://pay-sandbox.axepta.it';

  public hobexPaymentSubmitted: boolean = false;

  constructor(private utility: UtilityService) { super(); }

  ngAfterViewInit() {
    if (!!this.sumupTransactionId) {
      SumUpCard.mount({
        checkoutId: this.sumupTransactionId,
        locale: this.lingua == 'it' ? 'it-IT' : 'en-GB',
        onResponse: (type, body) => {
          switch (type) {
            case 'sent':
              this.setIsLoading(true);
              break;

            case 'invalid':
              break;

            case 'auth-screen':
              this.setIsLoading(false);
              break;

            case 'error':
              this.setIsLoading(false);
              this.dialogRef.close();
              break;

            case 'success':
              this.dialogRef.close(true);
              break;
          }
        }
      });
    } else if (!!this.hobexTransactionId) {
      if (window['wpwl'] !== undefined && window['wpwl'].unload !== undefined) {
        window['wpwl'].unload();
      }

      if (window['wpwlOptions'] !== undefined) {
        window['wpwlOptions'].locale = this.lingua;
      } else {
        let context = this;

        window['wpwlOptions'] = {
          locale: this.lingua,
          paymentTarget: 'hobexIFrame',
          shopperResultTarget: 'hobexIFrame',
          labels: { cvv: "CVV" },
          onAfterSubmit: function () {
            context.hobexPaymentSubmitted = true;
          }
        };
      }

      this.utility.loadExternalScript((this.isTest ? this.HOBEX_JS_URL_TEST : this.HOBEX_JS_URL) + this.hobexTransactionId, null, 'hobex-script').then(() => {
        this.setIsLoading(false);
      });
    } else if (!!this.axeptaTransactionId) {
      let axeptaClient = new AxeptaSDKClient(this.isTest ? this.AXEPTA_URL_TEST : this.AXEPTA_URL, this.axeptaApiKeySmartCheckout);
      axeptaClient.preparePayment(this.axeptaTransactionId);
    }
  }
}