import { Component, EventEmitter, Input, Output } from '@angular/core';
import { AngularEditorConfig } from '@kolkov/angular-editor';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-editor',
  templateUrl: './editor.component.html',
  styleUrls: ['./editor.component.scss']
})
export class EditorComponent extends BaseComponent {

  @Input() set text(value: string) {
    this._text = value;

    if (!!this._text) {
      let tags = this.utility.extractTagsFromHtml(this._text, ['style', 'link']);

      if (tags.length > 0) {
        this._text = this.utility.removeTagsFromHtml(this._text, ['style', 'link']);

        setTimeout(() => {
          this.textChange.emit(this._text);
          this.onHeadRemove.emit(tags.join());
        }, 200);
      }
    }
  };

  @Input() id: string;
  @Input() height: number = 200;
  @Input() showToolbar: boolean = true;
  @Input() disabled: boolean = false;

  @Output() onBlur: EventEmitter<any> = new EventEmitter();
  @Output() textChange: EventEmitter<string> = new EventEmitter();
  @Output() onHeadRemove: EventEmitter<string> = new EventEmitter();

  public editorFocused: boolean = false;
  public _text: string;

  public getEditorConfig(): AngularEditorConfig {
    let height = this.height;

    if (this.showToolbar && !this.disabled && !this.editorFocused) {
      height += 42;
    }

    return {
      editable: !this.disabled,
      spellcheck: true,
      height: height + 'px',
      minHeight: height + 'px',
      maxHeight: height + 'px',
      width: '100%',
      minWidth: '100%',
      translate: 'yes',
      enableToolbar: this.showToolbar && !this.disabled && this.editorFocused,
      showToolbar: this.showToolbar && !this.disabled && this.editorFocused,
      placeholder: '',
      defaultParagraphSeparator: '',
      defaultFontName: '',
      defaultFontSize: '',
      fonts: [
        { class: 'arial', name: 'Arial' },
        { class: 'times-new-roman', name: 'Times New Roman' },
        { class: 'calibri', name: 'Calibri' },
        { class: 'comic-sans-ms', name: 'Comic Sans MS' }
      ],
      customClasses: null,
      uploadUrl: '',
      uploadWithCredentials: false,
      sanitize: false,
      outline: false,
      toolbarPosition: 'top',
      toolbarHiddenButtons: [
        ['undo', 'redo'],
        ['insertImage', 'insertVideo', 'removeFormat', 'customClasses']
      ]
    };
  }

  constructor(private utility: UtilityService) {
    super();
  }
}
