import { Component, Input } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import * as detect from 'detect-file-type';

import { BaseComponent } from '../base-component/base-component';

@Component({
  selector: 'app-image-edit',
  templateUrl: './image-edit.component.html',
  styleUrls: ['./image-edit.component.scss']
})
export class ImageEditComponent extends BaseComponent {

  @Input() set _url(value: string) {
    this.url = value;

    if (!this.url) {
      this.deleteButtonEnabled = false;
    } else {
      this.deleteButtonEnabled = true;
    }
  };

  @Input() maximumFileSize: number = 0;
  @Input() title: string;
  @Input() dialogRef: MatDialogRef<{}, any>;
  @Input() blue: boolean;

  public url: string;
  public base64: string;
  public deleteButtonEnabled = true;

  constructor() {
    super();
  }

  fileSelected(fileSelect: any) {
    let file: File = fileSelect.files == null || fileSelect.files.length == 0 ? null : fileSelect.files[0];

    if (file != null && !!file.type && file.type.startsWith('image/')) {
      if (this.maximumFileSize <= 0 || file.size <= this.maximumFileSize) {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
          let base64 = reader.result.toString();

          if (!!base64) {
            detect.fromBuffer(Uint8Array.from(atob(base64.slice(base64.indexOf(',') + 1)), (c) => c.charCodeAt(0)), (err, result) => {
              if (!err && result != null && !!result.mime && result.mime.startsWith('image/')) {
                this.url = null;
                this.base64 = base64;
              } else {
                fileSelect.value = '';
                this.showError('globals.messages.fileNonValido');
              }
            });
          } else {
            fileSelect.value = '';
            this.showError('globals.messages.fileNonValido');
          }
        }
      } else {
        fileSelect.value = '';
        this.showError('globals.messages.fileTroppoGrande', { size: Math.floor(this.maximumFileSize / 1000) });
      }
    } else {
      fileSelect.value = '';
      this.showError('globals.messages.fileNonValido');
    }
  }

  delete() {
    this.showDialogConfirm('globals.eliminareImmagine')
      .then(si => {
        this.dialogRef.close('');
      },
        no => { }
      );
  }
}