import { AfterViewInit, Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { ColorPickerDirective } from 'ngx-color-picker';
import { ColorPickerModel } from 'src/app/shared/models/dto';

@Component({
  selector: 'app-color-picker',
  templateUrl: './color-picker.component.html',
  styleUrls: ['./color-picker.component.scss']
})
export class ColorPickerComponent implements AfterViewInit {

  @Input() options: ColorPickerModel;
  @Input() value: string;
  @Input() placeholder: string;

  @Output() valueChange: EventEmitter<string> = new EventEmitter();
  @Output() colorChanged: EventEmitter<any> = new EventEmitter();

  @ViewChild(ColorPickerDirective) colorPickerDirective: ColorPickerDirective;

  constructor() { }

  ngAfterViewInit() {
    if (this.colorPickerDirective) {
      this.colorPickerDirective.cpPosition = 'bottom';
      this.colorPickerDirective.cpUseRootViewContainer = true;

      if (this.options) {
        if (this.options.width) {
          this.colorPickerDirective.cpWidth = this.options.width;
        }

        if (this.options.height) {
          this.colorPickerDirective.cpHeight = this.options.height;
        }

        if (this.options.dialogDisplay) {
          this.colorPickerDirective.cpDialogDisplay = this.options.dialogDisplay;
        }

        if (this.options.position) {
          this.colorPickerDirective.cpPosition = this.options.position;
        }

        if (this.options.positionOffset) {
          this.colorPickerDirective.cpPositionOffset = this.options.positionOffset;
        }

        if (this.options.positionRelativeToArrow) {
          this.colorPickerDirective.cpPositionRelativeToArrow = this.options.positionRelativeToArrow;
        }

        if (this.options.useRootViewContainer != null) {
          this.colorPickerDirective.cpUseRootViewContainer = this.options.useRootViewContainer;
        }
      }
    }
  }
}