  public readonly ADDONPAYMENTS_API_URL = 'https://hpp.addonpayments.com/pay';
  public readonly ADDONPAYMENTS_API_URL_TEST = 'https://hpp.sandbox.addonpayments.com/pay';

  public readonly ADDON_PAYMENTS_LIGHTBOX_URL = '/assets/js/booking/rxp-hpp.js';

  public readonly SATISPAY_BUTTON_URL = 'https://online.satispay.com/web-button.js';
