  createPaypalOrder(): Promise<string> {
    let promise = new Promise<string>((resolve, reject) => {
      this.setIsLoading(true);
      
      let rq = PaymentRQ.getPaypalCreateRQ(this.oidAlbergo, this.oidPortale, this.model.modalitaPagamento.paymentId, this.cliente, this.lingua, this.arrivo, this.partenza, this.camere, this.extras, this.note, this.oidCodicePromozione, this.oidMotivoViaggio, this.model.modalitaPagamento.caparraTotale, this.model.privacy, this.model.newsletter, this.bookingSolutionUid, this.model.modalitaPagamento.paypalClientId, this.model.modalitaPagamento.paypalSecretKey, this.getImportoPagamento(), this.model.modalitaPagamento.isTest, this.descrizione, this.lingua == 'it' ? 'it-IT' : 'en-US');

      this.apiService.payment(rq).subscribe((rs: PaymentRS) => {
        if (rs.success && !!rs.transaction_id) {
          this.paypalTransactionId = rs.transaction_id;
          this.paypalToken = rs.token;
          resolve(rs.transaction_id);
        } else {
          reject(rs.error);
        }

        this.setIsLoading(false);
      });
    });

    return promise;
  }

